<template>
  <div id="MapSelect">
    <div style="overflow-y:auto;height:100%;width: 100%;">
      <table class="table table-hover table-striped">
        <thead>
        <tr>
          <td>序号</td>
          <td>描述数量</td>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,index) in blocks" v-bind:key="index" @click="click_block(item['id'])">
          <td>{{ item['id'] }}</td>
          <td>{{ item['len'] }}</td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: "MapSelect",
  methods: {
    "update_block_list": function () {
      this.blocks = window.get_map_info()['block_list'];
      console.log("更新地图区块显示列表",this.blocks)
    },
    "click_block": function (num) {
      window.set_block(num);
      window.show_block(num);
    }
  },
  mounted() {
    window.update_block = this.update_block_list;
  },
  data: function () {
    return {
      blocks: []
    };
  }
}

</script>

<style scoped>
#MapSelect{
  height: 100%;
  width: 100%;
}
</style>