<template>
  <div id="InputForm">
    <form id="dataForm">
      <div class="form-group" style="width: 100%;">
        <label for="inputData">地图描述</label>
        <textarea class="form-control" id="inputData" placeholder="请输入地图描述"
                  style="height: 300px"
                  :maxlength="max_text_length" v-model="describe">
          </textarea>
        <div style="text-align: left"><span>{{ describe.length }}</span>/<span>{{ max_text_length }}</span>
        </div>
      </div>
      <div class="btn-group" role="group">
        <button type="button" class="btn btn-primary operation-btn" @click="submit">提交</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "InputForm",
  data() {
    return {
      "max_text_length": 300,
      "describe": "",
    }
  },
  methods: {
    //获取地图描述信息
    "get_describe": function () {
      return this.describe;
    },
    //提交信息
    "submit": function () {
      if (this.describe.length === 0) {
        alert("描述不能为空");
        return;
      }
      let data = window.get_map_info();
      window.$.get(window.base_url + "/put/note/?blockid=" + data['block_index'].toString() + "&userdata=" + this.describe, function (e) {
        console.log(e);
        window.update_map_info();
      });
    }
  },
  created() {
  }
}
</script>

<style scoped>
#InputForm {
  width: 100%;
  height: 100%;
}

#inputData {
  width: 100%;
  height: 300px;
  font-size: 23px;
  resize: none;
}

.operation-btn {
  width: 90px;
  height: 30px;
}
</style>