<template>
  <div style="height: 100%;width: 100%;">
    <div id="map-view"/>
  </div>
</template>
<script type="text/javascript"
        src="https://api.map.baidu.com/api?v=1.0&type=webgl&ak=al1KD94r7DSYgLDaHslCYIXzLnajL30T"/>
<script src="https://cdn.bootcdn.net/ajax/libs/jquery/3.6.0/jquery.js"/>

<script>
export default {
  name: "MapView",
  data() {
    return {}
  },
  created() {

  },
  methods: {
    //构建地图
    "build_map": function () {
      let map = new window.BMapGL.Map("map-view");
      window.map_sdk = map;
      this.build_control();
    },
    //构建地图控件
    "build_control": function () {
      let map = window.map_sdk;
      //是否开启双击鼠标缩放
      map.disableDoubleClickZoom();

      //是否开启地图可拖拽缩放
      map.disableDragging();

      //是否开启地图旋转功能
      map.disableRotate();

      //是否开启鼠标滚轮缩放
      map.disableScrollWheelZoom();

      //是否开启地图倾斜功能
      map.disableTilt();

      map.addControl(new window.BMapGL.ScaleControl()); // 添加比例尺控件
      map.addControl(new window.BMapGL.MapTypeControl());// 添加地图类型控件
    },
    //地图栅格化
    "raster_map": function (blocks) {
      let map = window.map_sdk;
      for (let i = 0; i < blocks.length; i++) {
        let rect = blocks[i]['rect']
        let path = [
          new window.BMapGL.Point(rect['left-up']['lng'], rect['left-up']['lat']),
          new window.BMapGL.Point(rect['right-up']['lng'], rect['right-up']['lat']),
          new window.BMapGL.Point(rect['right-down']['lng'], rect['right-down']['lat']),
          new window.BMapGL.Point(rect['left-down']['lng'], rect['left-down']['lat']),
        ]
        var polygon = new window.BMapGL.Polygon(path, {strokeColor: "#0f0", strokeWeight: 1, strokeOpacity: 0.5});
        map.addOverlay(polygon);
      }
    },
    //显示指定区块
    "show_block": function (index) {
      let map = window.map_sdk;
      const data = window.get_map_info();
      const blocks = data['blocks'];
      if (index < 0 || index >= blocks.length) {
        console.log("显示区块 下标超界");
        return;
      }
      let lng = data['lng'] - data['step'] / 2;
      let lat = data['lat'] + data['step'] / 2;
      map.centerAndZoom(new window.BMapGL.Point(lng, lat), data['zoom'])
    }
  },
  mounted() {
    window.show_block = this.show_block;
    window.raster_map = this.raster_map;
    this.build_map();
  },
}
</script>

<style scoped>

#map-view {
  height: 100%;
  width: 100%;
}
</style>