<template>
  <div id="HistoryInfo">
    <div class="panel panel-default"
         style="overflow-y:auto;width: 100%;position: absolute;bottom: 400px;">
      <table class="table table-hover table-striped">
        <tbody id="history-list-table">
        <tr>
          <td>摘要</td>
<!--          <td>时间</td>-->
        </tr>
        <tr v-for="(item,index) in history" v-bind:key="index" @click="set_textarea_value(item)">
          <td>{{ item|setW(4) }}</td>
<!--          <td>{{ item['time'] }}</td>-->
        </tr>
        </tbody>
      </table>
    </div>
    <div style="width: 100%;position: absolute;bottom: 50px">
      <form id="dataForm">
        <div class="form-group">
          <label for="dataInfo">历史描述 <span id="time"></span></label>
          <textarea
              style="width: 100%;height: 300px;font-size: 23px;resize: none;"
              class="form-control" id="dataInfo"
              v-model="history_text" readonly="readonly">
          </textarea>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "HistoryInfo",
  filters: {
    //字符串限制输出
    setW(input, num) {
      if (input == null)
        return '';
      if (input.length <= num)
        return input;
      return input.substr(0, num) + "...";
    }
  },
  methods: {
    //设置描述信息
    'set_textarea_value': function (value) {
      this.history_text = value;
    },
    //更新历史数据信息
    "update_history_info": function () {
      let info = window.get_map_info();
      this.history = info['history_data']
    }

  }, mounted() {
    window.update_history_info = this.update_history_info;
  },
  data() {
    return {
      'history': [],
      "history_text": ""
    }
  },
}
</script>

<style scoped>
#HistoryInfo {
  width: 100%;
  height: 100%;
}
</style>