<template>
  <div class="panel panel-default" id="MapInfo">
    <div>
      <table class="table table-hover table-striped">
        <tbody>
        <tr>
          <td>当前区块号</td>
          <td>{{ block_index }}</td>
        </tr>
        </tbody>
      </table>
    </div>
    <div>
      <table class="table table-hover table-striped">
        <thead>
        <td>位置</td>
        <td>lng</td>
        <td>lat</td>
        </thead>
        <tbody>
        <tr>
          <td>左上角经纬度</td>
          <td>{{ lng |toFixed(number_bit) }}</td>
          <td>{{ lat |toFixed(number_bit) }}</td>
        </tr>
        <tr>
          <td>右上角经纬度</td>
          <td>{{ lng|toFixed(number_bit) }}</td>
          <td>{{ lat + step |toFixed(number_bit) }}</td>
        </tr>
        <tr>
          <td>左下角经纬度</td>
          <td>{{ lng - step|toFixed(number_bit) }}</td>
          <td>{{ lat |toFixed(number_bit) }}</td>
        </tr>
        <tr>
          <td>右下角经纬度</td>
          <td>{{ lng - step |toFixed(number_bit) }}</td>
          <td>{{ lat + step|toFixed(number_bit) }}</td>
        </tr>
        <tr>
          <td>中心点经纬度</td>
          <td>{{ lng - step / 2 |toFixed(number_bit) }}</td>
          <td>{{ lng - step / 2 |toFixed(number_bit) }}</td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: "MapInfo",
  methods: {
    //设置地图信息
    'set_map_info': function (data) {
      this.lng_range = data['lng'];
      this.lat_range = data['lat'];
      this.step = data['step'];
      this.zoom = data['zoom'];
      this.block_list = data['block_list'];
      this.blocks = data['blocks'];
      if (this.need_raster) {
        window.raster_map(this.blocks);
        this.need_raster = false;
      }
      if (this.blocks.length > 0 && this.block_index < this.blocks.length) {
        window.show_block(this.block_index);
        this.set_block(this.block_index);
      }
    },
    //设置当前区块左上角经纬度
    'set_position': function (lng, lat) {
      this.lng = lng;
      this.lat = lat;
    },
    //设置当前区块编号
    'set_block': function (index) {
      if (index < 0 || index >= this.blocks.length) {
        console.log("设置区块编号 超界");
        return;
      }
      console.log("设置当前区块编号为 " + index.toString());
      this.block_index = index;
      const block = this.blocks[index]['rect'];
      this.set_position(eval(block['left-up']['lng']), eval(block['left-up']['lat']));
      this.update_map_info_detail(index);
    },
    //更新地图信息
    "update_map_info": function () {
      console.log("更新地图信息");
      window.$.get(window.base_url + "/api/getMapInfo", function (e) {
        let data = eval(e);
        console.log(data);
        window.set_map_info(data);
        window.update_block();
      })
    },
    //更新地图详细信息
    "update_map_info_detail": function (index) {
      console.log("获取地图详细信息 编号:" + index.toString());
      window.$.get(window.base_url + "/get/note/?blockid=" + index.toString(), function (e) {
        let data = eval(e);
        console.log(data);
        window.set_history(data['note']);
        window.update_history_info();
      })
    },
    //设置历史信息
    "set_history": function (data) {
      console.log(data);
      this.history_data = data;
    },
    //获取地图信息
    "get_map_info": function () {
      return {
        //区块左上角
        "lng": this.lng,
        "lat": this.lat,
        //步长
        "step": this.step,
        //当前区块编号
        "block_index": this.block_index,
        //缩放等级
        "zoom": this.zoom,
        //历史数据
        "history_data": this.history_data,
        //区块简要信息
        "block_list": this.block_list,
        //区块信息
        "blocks": this.blocks,

      }
    }
  },
  created() {
    this.update_map_info();
  },
  mounted() {
    window.set_block = this.set_block;
    window.update_map_info = this.update_map_info;
    window.update_map_info_detail = this.update_map_info_detail;
    window.get_map_info = this.get_map_info;
    window.set_map_info = this.set_map_info;
    window.set_history = this.set_history;


  },
  data() {
    return {
      //缩放等级
      "zoom": 18,
      //经纬度范围
      "lng_range": {"min": 116.2, "max": 116.5},
      "lat_range": {"min": 39.8, "max": 40},
      //区块左上角
      "lng": 116.2,
      "lat": 39.8,
      //步长
      "step": 0.02,
      //区块
      'blocks': [],
      //区块简要信息
      "block_list": [],
      //当前区块编号
      "block_index": 0,
      //小数点保留位数
      "number_bit": 2,
      //历史数据
      "history_data": [],
      "need_raster": true,
    }
  },
  filters: {
    //小数格式化
    toFixed(input, num) {
      return Number(input).toFixed(num)
    }
  }
}
</script>

<style scoped>
#MapInfo {
  width: 100%;
  height: 100%;
}
</style>